
body{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; width: 100%; height: 100vh; -webkit-box-pack: center; -ms-flex-pack: center; justify-content: center; -webkit-box-align: center; -ms-flex-align: center; align-items: center; 
  background: -webkit-gradient(linear,right top, left bottom,from(#33a3dc),to(#411445)); 
  background: linear-gradient(top right,#33a3dc,#411445);
  text-align: center; overflow: hidden;
  font-size: 20px; margin: 0;
}
/*css流星动画*/
#stars {
        margin: 0 auto;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
}
.star {
        display: block;
  　　　width: 1px;
        background: transparent;
        position: relative;
        opacity: 0;
        /*过渡动画*/ 
        animation: star-fall 2s linear infinite;
        -webkit-animation: star-fall 2s linear infinite;
        -moz-animation: star-fall 2s linear infinite;
}
.star:after {
        content: '';
        display: block;
        border: 0px solid #fff;
        border-width: 0px 90px 2px 150px;
        border-color: transparent transparent transparent rgba(255, 255, 255, .5);
        -webkit-box-shadow: 0 0 1px 0 rgba(255, 255, 255, .1);
                box-shadow: 0 0 1px 0 rgba(255, 255, 255, .1);
        /*变形*/       
        transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -webkit-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -moz-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        transform-origin: 0% 100%;
        -webkit-transform-origin: 0% 100%;
        -moz-transform-origin: 0% 100%;
}
@-webkit-keyframes star-fall {
0% {
          opacity: 0;
          transform: scale(1) translate3d(0, 0, 0);
          -webkit-transform: scale(1) translate3d(0, 0, 0);
          -moz-transform: scale(1) translate3d(0, 0, 0);
}
50% {
          opacity: 1;
          transform: translate3d(-200px, 200px, 0);
          -webkit-transform:- translate3d(-200px, 200px, 0);
          -moz-transform: translate3d(-200px, 200px, 0);
}
100% {
          opacity: 0;
          transform: scale(1) translate3d(-300px, 300px, 0);
          -webkit-transform: scale(1) translate3d(-300px, 300px, 0);
          -moz-transform: scale(1) translate3d(-300px, 300px, 0);
}
}
@keyframes star-fall {
0% {
          opacity: 0;
          transform: scale(1) translate3d(0, 0, 0);
          -webkit-transform: scale(1) translate3d(0, 0, 0);
          -moz-transform: scale(1) translate3d(0, 0, 0);
}
50% {
          opacity: 1;
          transform: translate3d(-200px, 200px, 0);
          -webkit-transform:- translate3d(-200px, 200px, 0);
          -moz-transform: translate3d(-200px, 200px, 0);
}
100% {
          opacity: 0;
          transform: scale(1) translate3d(-300px, 300px, 0);
          -webkit-transform: scale(1) translate3d(-300px, 300px, 0);
          -moz-transform: scale(1) translate3d(-300px, 300px, 0);
}
}




